<template>
    <div class="boot-page" >
        <!----->
        <div class="bid overflowAuto">
            <table>
                <tr>
                    <th class="firstTh"></th>
                    <th class="secondTh"></th>
                    <th class="thirdTh"></th>
                    <th class="fourthTh"></th>
                    <th class="lastTh"></th>
                </tr>
                <tr v-for="(item,index) in bid">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd" @click="showPopup()">
                        {{item.bidName}}
                    </td>
                    <td>
                        {{item.itemType}}
                    </td>
                    <td>
                        {{item.bidEnt}}
                    </td>
                    <td  class="lastTd">
                        {{item.releaseDate}}
                    </td>
                </tr>
            </table>
            <!---->
            <el-dialog  :visible.sync="dialogVisible">
                <p class="title"></p>
                <!---->
                <div class="dialog-con">
                    <div class="writtenVerdict">
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.bidName}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.itemType}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.admArea}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.province}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.releaseDate}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails. bidPublicityType}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.proContact}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.proContactNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.bidEnt}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.bidEntAddress}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.bidEntContact}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.agencyName}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.agencyAddress}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.agencyContact}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.entUrl}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{bidDetails.mainText}}</span></p>
                        </div>
                    </div>
                </div>
            </el-dialog>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'winBidList',
        data () {
            return {
                entName: this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                dialogVisible: false,
                bid:[],
                bidDetails:{}
            }
        },
        mounted(){
            api.Get('/cloudMap/opStatus/getBidInfoList',{
                "entId": 1,
                "entName": this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res);
                this.totalRecord=res.totalRecord;
                this.bid=res.data;
            })
        },
        methods:{
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                api.Get('/cloudMap/opStatus/getBidInfoList',{
                    "entId": 1,
                    "entName": this.entName,
                    "pageSize":50,
                    "page":1
                }).then((res)=>{
                    console.log(res);
                    this.totalRecord=res.totalRecord;
                    this.bid=res.data;
                })
            },
            showPopup( ){
                this.dialogVisible = true;
                api.Get('/cloudMap/opStatus/getBidDetails',{
                    "bidId": 1,
                    "entName": this.entName
                }).then((res)=>{
                    console.log(res)
                    this.bidDetails =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    @import 'assets/css/dialog.css';
    /**/
    .dialog-item .item-value{
        text-align: left;
    }
    .dialog-item .item-value span{
        text-align: left;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }
    .bid .secondTd span{
        text-decoration: underline;
        cursor: pointer;
    }
    .bid .lastTd{
        border-right:1px solid #1e417a;
    }
    .bid .firstTh{
        width:10%;
    }
    .bid .secondTh{
        width:25%;
    }
    .bid .thirdTh{
        width:15%;
    }
    .bid .fourthTh{
        width:35%
    }
    .bid .lastTh{
        width:15%
    }
    .secondTd{
        cursor: pointer;
        text-decoration: underline;
    }
</style>



