        vertical-align: top;
        text-indent:2em;
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .entInfo td.borderRightNone{
        border-right:none;
    }
    .entInfo td.borderRightNone:hover{
        border-right:none;
    }
</style>



