<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <div class="text">
                <p>{{datalist.articleCon}}</p>
            </div>
        </div>

    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'nonSpecificRecord',
        data () {
            return {
                datalist: {
//                    "articleCon":"3"
                }
            }
        },
        methods:{
            getList(params){//
                api.Get('/cloudMap/fda/getNonSpecificRecordDetails',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                })
                    .catch(err=>{
                        console.log('');
                    })
            }
        },
        mounted(){
            this.getList({//
                entId:1,
                entName:this.$router.currentRoute.params.entName,
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .boot-page{
        width:100%;
        height:100%;
        background:rgba(88,143,255,0.15);
        border:1px solid rgba(99,150,255,0.35);
    }
    .text{
        color:#fff;
        margin:20px 40px;
        line-height:32px;
    }
    .text p{
        text-indent:2em;
        margin-bottom:30px;
    }
</style>



