<template>
    <div class="boot-page" >
        <!---->
        <div class="patent">
            <div class="div-dataList">
            <table id="tb_data">
                <tr>
                    <th class="firstTh"></th>
                    <th class="secondTh"></th>
                    <th class="thirdTh"></th>
                    <th class="fourthTh"></th>
                    <th class="fifthTh"></th>
                </tr>
                <tr v-for="(item,index) in patent">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd">
                            {{item.applyNumber}}
                    </td>
                    <td>
                            {{item.patentType}}
                    </td>
                    <td>
                            {{item.publicityDate}}
                    </td>
                    <td  class="lastTd">
                            <i @click="showPopup(item.id)">{{item.abstractTit}} </i><br/>
                            {{item.abstract}}
                    </td>
                </tr>
            </table>
            </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
            </div>
        </div>

        <!---->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title"></p>
            <!---->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.patent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.patentType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.appNumber}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.appDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.publicNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.publicityDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.classificationNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.priority}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.applicant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.inventor}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.agency}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.agent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.entAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.legalStatus}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.intApplication}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.intPublication}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.abstract}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'patentList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                patent:[
//                    {
//                        "applyNumber":"24532445234",
//                        "patentType":"",
//                        "publicityDate":"2012-11-11",
//                        "abstractTit":"",
//                        "abstract":""
//                    },
//                    {
//                        "applyNumber":"24532445234",
//                        "patentType":"",
//                        "publicityDate":"2012-11-11",
//                        "abstractTit":"",
//                        "abstract":""
//                    }
                ],
                abstractList:{
//                    "name":"",
//                    "patentType":"",
//                    "appNumber":"CN201510061313.2",
//                    "appDate":"2015-02-05",
//                    "publicNum":"CN104610018A",
//                    "publicDate":"2015-02-05",
//                    "classificationNum":"C07C9/762006.1",
//                    "priority":"--",
//                    "applicant":"",
//                    "inventor":",,",
//                    "agency":" 31272",
//                    "agent":"",
//                    "address":"xx   xx   xx",
//                    "legalStatus":"",
//                    "intApplication":"--",
//                    "intPublication":"--",
//                    "abstract":""
                }
            }
        },
        mounted(){
            api.Get('/cloudMap/inAssetsInfo/getPatentList',{entId:1,entName:this.$router.currentRoute.params.entName,page:1,pageSize:this.pageSize}).then(res=>{
                this.patent=res.data;
                this.totalRecord=res.totalRecord;
                //console.log(this.$router.currentRoute.params.entName);
                console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true
                api.Get('/cloudMap/inAssetsInfo/getPatentInfo',
                    {id:id,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                    this.abstractList=res.data;
                    //console.log(id);
                    console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getPatentList',
                    {entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.patent=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });

            },
        },
        components:{

        }
    }
        </script>

        <!-- Add "scoped" attribute to limit CSS to this component only -->
        <style scoped>
        @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .patent .firstTh{
        width:10%;
    }
    .patent .secondTh{
        width:15%;
    }
    .patent .thirdTh{
        width:15%;
    }
    .patent .fourthTh{
        width:16%
    }
    .patent .fifthTh{
        width:44%
    }
    .patent i{
        text-decoration: underline;
        cursor: pointer;
        display: inline-block;
        margin-bottom: 4px;
    }
    .lastTd{
        padding: 10px 20px;
    }
</style>



