<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="w100"></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(list,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td>
                        <a @click="showPopup(list.id)">{{list.caseNo}}</a>
                    </td>
                    <td>{{list.beDescription}}</td>
                    <td>{{list.unitExecution}}</td>
                    <td>{{list.releaseDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!---->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!---->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!---->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">/</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.beName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.gender}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.age}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">/</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.idCard}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.executionCourt}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.province}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enBasisNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingTime}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.caseNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.unitExecution}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.takeObligation}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.executeResult}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.beDescription}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.releaseDate}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'brokenPromisesDetails',
        data () {
            return {
                dialogVisible: false,
                title:'',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [],
                dataDetails:[]
            }
        },
        methods:{
            handleCurrentChange(val) {//
                console.log(`: ${val}`);
                this.getList({
                    entId: 1,
                    entName: this.$router.currentRoute.params.entName,
                    page: val,
                    pageSize: this.pageSize
                })
            },
            showPopup(id){//
                this.dialogVisible = true;
                console.log(id);
                api.Get('/cloudMap/court/getDishonestyDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "id":id
                })
                    .then(res=>{
                        console.log(''+res.code);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('');
                    })
            },
            getList(params){//
                api.Get('/cloudMap/court/getDishonestyList',params).then((res)=>{
                    console.log(res);
                    console.log(''+res.code);
                    this.datalist = res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('');
                    })
            }
        },
        mounted(){
            this.getList({//
                entId: 1,
                entName: this.$router.currentRoute.params.entName,
                page: 1,
                pageSize: this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



