<template>
    <div class="boot-page" >
        <!---->
        <div class="admPunishInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh"></th>
                    <th class="secondTh"></th>
                    <th class="thirdTh"></th>
                    <th class="fourthTh"></th>
                    <th class="lastTh"></th>
                </tr>
                <tr v-for="(item,index) in admPunishInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd" @click="showPopup()">
                            {{item.decDocNumber}}
                    </td>
                    <td>
                        <span>
                            {{item.illegalActType}}
                        </span>
                    </td>
                    <td class="fourthTd">
                        <span>
                            {{item.punishCon}}
                        </span>
                    </td>
                    <td  class="lastTd">
                            {{item.publicityDate}}
                    </td>
                </tr>
            </table>
            <!---->
            <el-dialog  :visible.sync="dialogVisible">
                <p class="title"></p>
                <!---->
                <div class="dialog-con">
                    <div class="writtenVerdict">
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.decDocNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.punishDoc}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.decisionAdm}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.decisionDate}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.legalRepresentative}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.illegalActType}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.punishCon}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl"></span>
                            </h4>
                            <p class="item-value"><span>{{admPunishDetails.publicityDate}}</span></p>
                        </div>
                    </div>
                </div>
            </el-dialog>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'admPunishInfoList',
        mounted(){
            api.Get('/cloudMap/business/getAdmPunishList',{
                "entId": 1,
                "entName":this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.admPunishInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                dialogVisible: false,
                admPunishInfo:[],
                admPunishDetails:{}
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
                api.Get('/cloudMap/business/getAdmPunishList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.admPunishInfo =res.data;
                })
            },
            showPopup( ){
                this.dialogVisible = true;
                api.Get('/cloudMap/business/getAdmPublishDetails',{
                    "entId": 1,
                    "entName":this.entName
                }).then((res)=>{
                    console.log(res)
                    this.admPunishDetails =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    @import 'assets/css/dialog.css';
    /**/
    .dialog-item .item-value{
        text-align: left;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }
    /**/
    .admPunishInfo .firstTd{
        border-left:1px solid #1e417a;
    }
    .admPunishInfo .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .admPunishInfo .lastTd{
        border-right:1px solid #1e417a;
    }
    .admPunishInfo .firstTh{
        width:10%;
    }
    .admPunishInfo .secondTh{
        width:20%;
    }
    .admPunishInfo .thirdTh{
        width:20%;
    }
    .admPunishInfo .fourthTh{
        width:30%
    }
    .admPunishInfo .lastTh{
        width:20%
    }
    .admPunishInfo .td span{
        text-align: left;
        float: right;
        width:calc(100% - 15px);
        margin: 10px 0;
        margin-right:0px;
        padding-right: 15px;
        border-right: 1px solid #214c99;
        display: inline-block;
    }
    .secondTd,.fourthTd{
        padding:10px 20px;
    }
    tr:hover .secondTd,tr:hover .fourthTd{
        padding:10px 20px;
    }

</style>



