<template>
    <div class="boot-page" >
        <!---->
        <div class="trademark">
            <div class="div-dataList">
            <table>
                <tr>
                    <th class="firstTh"></th>
                    <th class="secondTh"></th>
                    <th class="thirdTh"></th>
                    <th class="fourthTh"></th>
                    <th class="fifthTh"></th>
                    <th class="sixthTh"></th>
                    <th class="seventhTh"></th>
                </tr>
                <tr v-for="(item,index) in trademark">
                    <td class="firstTd">{{index+1}}</td>
                    <td>
                        <img  alt="" src="../../assets/img/trademark.png" /><!--:src="item.trademark"-->
                    </td>
                    <td>
                            {{item.brandName}}
                    </td>
                    <td class="fourthTd" @click="showPopup(item.id)">
                            {{item.regNumber}}
                    </td>
                    <td>
                            {{item.trademarkType}}
                    </td>
                    <td>
                            {{item.trademarkState}}
                    </td>
                    <td  class="lastTd">
                            {{item.applicationDate}}
                    </td>
                </tr>
            </table>
            </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="this.currentPage"
                        :page-size="this.pageSize"
                        layout="prev, pager, next, jumper"
                        :total="this.totalRecord">
                </el-pagination>
            </div>
        </div>

        <!---->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title"></p>
            <!---->
            <div class="dialog-con">
                <div class="dialogImg">
                    <img src="../../assets/img/patent.png" alt="">
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.brandName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.regNumber}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.trademarkType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.trademarkState}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.applicant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.applicationDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">/</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.commodityList}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'trademarkList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                trademark:[
//                    {
//                        "trademark":"../../assets/img/trademark.png",
//                        "productName":"",
//                        "regNumber":"1234567876",
//                        "type":"37-",
//                        "status":"",
//                        "applyTime":"2012-11-11"
//                    },
//                    {
//                        "trademark":"../../assets/img/trademark.png",
//                        "productName":"",
//                        "regNumber":"1234567876",
//                        "type":"37-",
//                        "status":"",
//                        "applyTime":"2012-11-11"
//                    },
                ],
                tradeList:{
//                    "brandName":"",
//                    "regisNum":"24352345234",
//                    "trademarkType":"37 - ",
//                    "trademarkState":"",
//                    "applicant":"",
//                    "applicationDate":"2016-10-26",
//                    "commodityList":"123456"
            }
            }
        },
        mounted(){
            api.Get('/cloudMap/inAssetsInfo/getTrademarkList',{page:1,pageSize:this.pageSize,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                this.trademark=res.data;
                this.totalRecord=res.totalRecord;
                console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true;
                api.Get('/cloudMap/inAssetsInfo/getTrademarkInfo',{id:id,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                    this.tradeList=res.data;
                    //console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getTrademarkList',{entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.trademark=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });

            },
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .trademark .firstTh{
        width:10%;
    }
    .trademark .secondTh{
        width:15%;
    }
    .trademark .thirdTh{
        width:15%;
    }
    .trademark .fourthTh{
        width:12%
    }
    .trademark .fifthTh{
        width:14%
    }
    .trademark .sixthTh{
        width:18%
    }
    .trademark .seventhTh{
        width:15%
    }
    .trademark .fourthTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .trademark img{
        display: inline-block;
        width: 104px;
        height: 53px;
        margin: 4px;
        line-height:53px;
        border:none;
    }
</style>



