<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50"></th>
                        <th></th>
                        <th></th>
                        <th class="w100"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(item.id)">{{item.recordNum}}</a>
                        </td>
                        <td>{{item.proCategory}}</td>
                        <td>{{item.filingDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!---->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!---->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!---->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.recordNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proCategory}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicantName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicantCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicantAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productionAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.agent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.agentRegisAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proClassifiName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">/</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.specifications}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.MainComponent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.intendedUse}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingUnit}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.alteration}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl"></span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingState}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'medicalInstrumentsRecord',
        data () {
            return {
                dialogVisible: false,
                title:'',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
//                    {
//                        "id": 1,
//                        "recordNum": "xxxxxxxx",
//                        "proCategory": "xxxxxxxxxxxxxxxxxx",
//                        "filingDate": "2016-12-1"
//                    },
//                    {
//                        "id": 1,
//                        "recordNum": "xxxxxxxx",
//                        "proCategory": "xxxxxxxxxxxxxxxxxx",
//                        "filingDate": "2016-12-1"
//                    },
//                    {
//                        "id": 1,
//                        "recordNum": "xxxxxxxx",
//                        "proCategory": "xxxxxxxxxxxxxxxxxx",
//                        "filingDate": "2016-12-1"
//                    }
                ],
                dataDetails:{
//                    "id":1,
//                    "recordNum":"xxxxx",
//                    "proCategory":"xxxx",
//                    "applicantName":"xxx",
//                    "applicantCode":"xxxx",
//                    "applicantAds":"xxx",
//                    "productionAds":"xxx",
//                    "agent":"xxx",
//                    "agentRegisAds":"xxx",
//                    "proClassifiName":"xxx",
//                    "specifications":"xxx",
//                    "proValidity":"xxx",
//                    "MainComponent":"xxx",
//                    "intendedUse":"xxx",
//                    "remarks":"xxx",
//                    "filingUnit":"xxx",
//                    "filingDate":"xxx",
//                    "alteration":"xxx",
//                    "filingState":"xxx"
                }
            }
        },
        methods:{
            getList(params){//
                api.Get('/cloudMap/fda/getmedicalInstrumentsRecordList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('');
                    })
            },
            handleCurrentChange(val) {//
                console.log(`: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(medicalId){//
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getmedicalInstrumentsRecordDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    medicalId: medicalId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



